import 'tms-semantic-ui';
import {
    default as toastr
}
from 'toastr';
import {
    default as wurl
}
from 'wurl';
import 'lodash';
import utils from 'common/common-utils';

export class App {
    constructor() {
        window.toastr = toastr;
        window.wurl = wurl;
        window.utils = utils;
        this.init();
    }

    init() {
        // ui form 
        _.extend($.fn.form.settings.prompt, {
            empty: '{name}',
            checked: '{name}',
            email: '{name}',
            url: '{name}URL',
            regExp: '{name}',
            integer: '{name}',
            decimal: '{name}',
            number: '{name}',
            is: '{name}"{ruleValue}"',
            isExactly: '{name}"{ruleValue}"',
            not: '{name}"{ruleValue}"',
            notExactly: '{name}"{ruleValue}"',
            contain: '{name}"{ruleValue}"',
            containExactly: '{name}"{ruleValue}"',
            doesntContain: '{name}"{ruleValue}"',
            doesntContainExactly: '{name}"{ruleValue}"',
            minLength: '{name}{ruleValue}',
            length: '{name}{ruleValue}',
            exactLength: '{name}{ruleValue}',
            maxLength: '{name}{ruleValue}',
            match: '{name}{ruleValue}',
            different: '{name}{ruleValue}',
            creditCard: '{name}',
            minCount: '{name}{ruleValue}',
            exactCount: '{name}{ruleValue}',
            maxCount: '{name} {ruleValue}'
        });
    }

    /**
     * 
     * @param  {[object]} config 
     * @param  {[object]} router 
     */
    configureRouter(config, router) {

        config.map([{
            route: ['pwd-reset'],
            name: 'reset',
            moduleId: 'user/user-pwd-reset',
            nav: false,
            title: ''
        }, {
            route: ['register'],
            name: 'register',
            moduleId: 'user/user-register',
            nav: false,
            title: ''
        }, {
            route: ['chat-direct/:username'],
            name: 'chat-direct',
            moduleId: 'chat/chat-direct',
            nav: false,
            title: ''
        }, {
            route: ['login'],
            name: 'login',
            moduleId: 'user/user-login',
            nav: false,
            title: ''
        },  {
            route: ['test'],
            name: 'test',
            moduleId: 'test/test-lifecycle',
            nav: false,
            title: ''
        }, {
            route: '',
            redirect: 'chat-direct/admin'
        }]);

        this.router = router;

    }
}
