/**
 * 
 */
export class ViewModel {

    header = '';

    /**
     * (ViewModel)
     * @param  {[object]} params                
     * @param  {[object]} routeConfig           
     * @param  {[object]} navigationInstruction 
     * @return {[promise]}                      (promise), bindattach(view), .
     */
    activate(params, routeConfig, navigationInstruction) {

        if (params.id) {
            this.token = params.id;

            this.isReq = true;
            this.header = ',...!';
            http.fetch('/free/user/register/activate', {
                method: 'post',
                body: json({
                    token: this.token
                })
            }).then((resp) => {
                if (resp.ok) {
                    resp.json().then((data) => {
                        if (data.success) {
                            this.header = ',!';
                        } else {
                            this.header = '!';
                            toastr.error(data.data, '!');
                        }
                    });
                    this.isReq = false;
                }
            });
        }
    }

    /**
     * DOM
     */
    attached() {

        $(this.fm).form({
            on: 'blur',
            inline: true,
            fields: {
                username: {
                    identifier: 'username',
                    rules: [{
                        type: 'empty'
                    }, {
                        type: 'minLength[3]'
                    }, {
                        type: 'regExp',
                        value: /^[a-z]+[a-z0-9\.\-_]*[a-z0-9]+$/,
                        prompt: '.-_,,'
                    }]
                },
                pwd: {
                    identifier: 'pwd',
                    rules: [{
                        type: 'empty'
                    }, {
                        type: 'minLength[8]'
                    }]
                },
                name: {
                    identifier: 'name',
                    rules: [{
                        type: 'empty'
                    }, {
                        type: 'maxLength[20]'
                    }]
                },
                mail: {
                    identifier: 'mail',
                    rules: [{
                        type: 'empty'
                    }, {
                        type: 'email'
                    }]
                }
            }
        });

    }

    okHandler() {

        if (!$(this.fm).form('is valid')) {
            toastr.error('!');
            return;
        }

        this.isReq = true;
        http.fetch('/free/user/register', {
            method: 'post',
            body: json({
                username: this.username,
                pwd: this.pwd,
                name: this.name,
                mail: this.mail,
                baseUrl: utils.getBaseUrl(),
                path: wurl('path')
            })
        }).then((resp) => {
            if (resp.ok) {
                resp.json().then((data) => {
                    if (data.success) {
                        toastr.success(',!');
                        _.delay(() => {
                            window.location = "/admin/login";
                        }, 2000);
                    } else {
                        toastr.error(data.data, '!');
                        this.isReq = false;
                    }
                });

            }
        });

    }

}
