/**
 * 
 * : 6s, 5min, , 1min(10),   +6s, 
 *  +6/, , . , 6s.
 * @return {[type]} [description]
 */
var minInterval = 6000; //  6s
var maxInterval = 300000; // 5min
var incInterval = 6000; //  6s

var tolerate = 10; // (), , .

var timer = null; // 

var inc = 0; // 

var interval = minInterval; // 

var _pollCb = null;
var _errCb = null;
var _isPause = false; // 

function oneHandler() {

    if (_isPause) {
        return;
    }

    try { // , .
        _pollCb && _pollCb(_reset, _stop);
    } catch (e) {
        _errCb && _errCb(_reset, _stop, e);

        // TODO for debugging
        console.log(': ' + e);
    }
}

/**
 * 
 * @param  {[Function]} pollCb 
 * @param  {[Function]} errCb  
 */
function _start() {
    // TODO for debugging
    console.log('poll start...');

    _isPause = false;

    oneHandler();
    timer = setInterval(function() {
        inc++;
        oneHandler();
        // TODO for debugging
        // console.log(interval);

        if (inc > tolerate) { // 

            interval = minInterval + (incInterval * (inc - tolerate));

            if (interval <= maxInterval) { // , 
                clearInterval(timer);
                _start();
            }
        }
    }, interval);
}

function _stop() {
    // TODO for debugging
    console.log("poll stop...");

    inc = 0;
    interval = minInterval;
    _isPause = false;
    clearInterval(timer);
    timer = null;
}

function _reset() {
    // TODO for debugging
    console.log("poll reset...");

    _stop();
    _start();
}

function _pause() {
    // TODO for debugging
    console.log("pause reset...");
    _isPause = true;
}

export default {
    start: function(pollCb, errCb) {
        if (timer) {
            _stop();
        }
        _pollCb = pollCb;
        _errCb = errCb;
        _start();
    },
    reset: function() {
        _reset();
    },
    stop: function() {
        _stop();
    },
    pause: function() {
        _pause();
    }
};
