/**
 * 
 */
export class UserPwdReset {

    mail = '';
    pwd = '';

    isReq = false;

    token = utils.urlQuery('id');

    resetPwdHandler() {

        if (!$(this.fm).form('is valid')) {
            toastr.error('!');
            return;
        }

        this.isReq = true;
        http.fetch('/free/user/pwd/reset', {
            method: 'post',
            body: json({
                mail: this.mail,
                baseUrl: utils.getBaseUrl(),
                path: wurl('path')
            })
        }).then((resp) => {
            if (resp.ok) {
                resp.json().then((data) => {
                    if (data.success) {
                        toastr.success('!');
                        _.delay(() => {
                            window.location = "/admin/login";
                        }, 2000);
                    } else {
                        toastr.error(data.data, '!');
                        this.isReq = false;
                    }
                });
            }

        });
    }

    newPwdHandler() {

        if (!$(this.fm2).form('is valid')) {
            toastr.error('!');
            return;
        }

        this.isReq = true;
        http.fetch('/free/user/pwd/new', {
            method: 'post',
            body: json({
                token: this.token,
                pwd: this.pwd
            })
        }).then((resp) => {
            if (resp.ok) {
                resp.json().then((data) => {
                    if (data.success) {
                        toastr.success('!');
                        _.delay(() => {
                            window.location = "/admin/login";
                        }, 2000);
                    } else {
                        toastr.error(data.data, '!');
                        this.isReq = false;
                    }
                });
            }
            
        });
    }

    /**
     * DOM
     */
    attached() {

        $(this.fm).form({
            on: 'blur',
            inline: true,
            fields: {
                mail: ['empty', 'email']
            }
        });

        $(this.fm2).form({
            on: 'blur',
            inline: true,
            fields: {
                mail: ['empty', 'minLength[8]']
            }
        });
    }

}
